ol,
output,
p,
pre,
q,
ruby,
s,
samp,
section,
small,
span,
strike,
strong,
sub,
summary,
sup,
table,
tbody,
td,
tfoot,
th,
thead,
time,
tr,
tt,
u,
ul,
var,
video {
  margin: 0;
  padding: 0;
  border: 0;
  font-size: 100%;
  font: inherit;
  vertical-align: baseline; }

/* HTML5 display-role reset for older browsers */
article,
aside,
details,
figcaption,
figure,
footer,
header,
hgroup,
menu,
nav,
section {
  display: block; }

body {
  line-height: 1; }

ol,
ul {
  list-style: none; }

blockquote,
q {
  quotes: none; }

blockquote:after, blockquote:before {
  content: '';
  content: none; }

q:after, q:before {
  content: '';
  content: none; }

table {
  border-collapse: collapse;
  border-spacing: 0; }

a {
  text-decoration: none; }

a:hover {
  text-decoration: none; }

body {
  font-family: 'latolight', sans-serif;
  font-size: 62.5%;
  -webkit-box-sizing: border-box;
  -moz-box-sizing: border-box;
  box-sizing: border-box;
  overflow-x: hidden; }

a {
  color: #000;
  text-decoration: none; }

a:hover {
  color: #000;
  text-decoration: none; }

.panel {
  position: relative; }

.inner {
  padding: 1.5% 2%;
  width: 90%;
  max-width: 1368px;
  margin: 0 auto; }

h4 {
  margin-top: 1em;
  font-size: 1.6em;
  letter-spacing: 0.1em;
  font-weight: 800;
  text-transform: uppercase; }

.panel:after {
  content: " ";
  display: table; }

.panel:after {
  clear: both; }

/*SOCIAL BUTTON*/
.social-buttons {
  position: fixed;
  top: 30%;
  left: 1%;
  z-index: 1; }

.icon {
  display: block;
  font-size: 14px;
  fill: currentColor;
  color: #c86512;
  margin-bottom: 2em; }

.icon:hover {
  color: #f4b47f; }

/* THE FIRST SECTION */
.home {
  position: relative;
  height: 100vh;
  color: #fff;
  background-color: #aaa;
  background: url("../img/main_bg_full.jpg") no-repeat;
  background-size: cover;
  background-attachment: fixed; }
  .home .inner {
    padding-top: 0.5%; }

.header .logo {
  width: 96px;
  height: 56px; }

#logo__razor {
  fill: currentColor;
  color: transparent;
  stroke: #fff;
  stroke-width: 10; }

#logo__okama {
  fill: #fff;
  stroke: #fff;
  stroke-width: 0; }

@keyframes offset {
  to {
    stroke-dashoffset: 0; } }

.outline {
  animation: offset 2s linear forwards;
  stroke-dasharray: 2580px;
  stroke-dashoffset: 2580px;
  animation-delay: 1s; }

p {
  font-size: 1.4em;
  margin-bottom: 1em; }

.main-menu {
  float: right;
  padding-top: 20px; }
  .main-menu .nav li {
    display: inline-block; }
  .main-menu .nav a {
    margin-left: 20px;
    font-size: 1.4em;
    color: #fff;
    text-decoration: none;
    padding-bottom: 3px;
    text-transform: uppercase; }
  .main-menu .nav a:hover {
    border-bottom: 1px solid #fff; }

/*begin hamburger*/
#touch-menu {
  display: none; }

#nav-icon1 {
  width: 30px;
  height: 30px;
  position: relative;
  margin-top: 15px;
  -webkit-transform: rotate(0deg);
  -moz-transform: rotate(0deg);
  -o-transform: rotate(0deg);
  transform: rotate(0deg);
  -webkit-transition: 0.5s ease-in-out;
  -moz-transition: 0.5s ease-in-out;
  -o-transition: 0.5s ease-in-out;
  transition: 0.5s ease-in-out;
  cursor: pointer; }

#nav-icon1 span {
  display: block;
  position: absolute;
  height: 2px;
  width: 100%;
  background: #bebebe;
  border-radius: 9px;
  opacity: 1;
  left: 0;
  -webkit-transform: rotate(0deg);
  -moz-transform: rotate(0deg);
  -o-transform: rotate(0deg);
  transform: rotate(0deg);
  -webkit-transition: 0.25s ease-in-out;
  -moz-transition: 0.25s ease-in-out;
  -o-transition: 0.25s ease-in-out;
  transition: 0.25s ease-in-out; }

#nav-icon1 span:nth-child(1) {
  top: 0; }

#nav-icon1 span:nth-child(2) {
  top: 10px; }

#nav-icon1 span:nth-child(3) {
  top: 20px; }

#nav-icon1.open span:nth-child(1) {
  top: 15px;
  -webkit-transform: rotate(135deg);
  -moz-transform: rotate(135deg);
  -o-transform: rotate(135deg);
  transform: rotate(135deg); }

#nav-icon1.open span:nth-child(2) {
  opacity: 0;
  left: -60px; }

#nav-icon1.open span:nth-child(3) {
  top: 15px;
  -webkit-transform: rotate(-135deg);
  -moz-transform: rotate(-135deg);
  -o-transform: rotate(-135deg);
  transform: rotate(-135deg); }

/*end hamburger*/
.slogan {
  max-width: 640px;
  text-align: center;
  margin: 20vh auto 0; }
  .slogan h1 {
    font-size: 36px;
    font-weight: 800; }
    .slogan h1 span {
      font-size: 32px; }
  .slogan .tagline {
    margin-top: 20px;
    line-height: 1.5em;
    font-size: 1.5em;
    letter-spacing: 0.02em;
    padding: 0; }

.scroll {
  height: 90px;
  overflow: auto;
  margin: auto;
  position: absolute;
  left: 0;
  bottom: 0;
  right: 0;
  font-size: 1.3em;
  transition: font-size 0.5s; }
  .scroll a {
    color: #fff;
    text-decoration: none;
    margin-bottom: 30px; }

.scroll:hover {
  text-decoration: none;
  font-size: 1.5em;
  color: #fff; }

.scroll:after {
  content: '';
  width: 40px;
  height: 40px;
  position: absolute;
  margin: 1px auto;
  top: 20px;
  right: 0;
  left: 0;
  -webkit-animation: 3s arrow infinite ease;
  animation: 3s arrow infinite ease; }

.scroll:after {
  border-right: 2px solid #bbb;
  border-bottom: 2px solid #bbb;
  -ms-transform: rotate(45deg);
  -webkit-transform: rotate(45deg);
  transform: rotate(45deg); }

@-webkit-keyframes arrow {
  0%,
  100% {
    top: 10px; }
  50% {
    top: 30px; } }

@keyframes arrow {
  0%,
  100% {
    top: 10px; }
  50% {
    top: 30px; } }

/****** Main ********/
/* THE SECOND SECTION */
/* Filtering of tiles */
.filter {
  text-align: center;
  margin-top: 3em; }
  .filter nav {
    text-align: center; }

.filter a {
  display: inline-block;
  padding: 5px 15px;
  font-size: 1.2em;
  font-weight: 500;
  text-transform: uppercase;
  text-decoration: none; }

.filter a.active {
  color: #FFFFFF;
  background: #1E2023; }

.is-animated {
  animation: 0.6s zoom-in; }

@keyframes zoom-in {
  0% {
    transform: scale(0.1); }
  100% {
    transform: none; } }

/* ENDfiltering of tiles */
/*hover animation for tiles*/
.hover-animation li {
  position: relative;
  overflow: hidden;
  margin-bottom: 3em; }

.hover-animation li:hover .caption {
  opacity: 1; }

.hover-animation li:hover img {
  opacity: 1;
  transform: scale(1.1, 1.1);
  -webkit-transform: scale(1.1, 1.1);
  -moz-transform: scale(1.1, 1.1);
  -ms-transform: scale(1.1, 1.1);
  -o-transform: scale(1.1, 1.1); }

.hover-animation img {
  float: left;
  z-index: 4;
  width: 100%;
  height: 100%;
  -webkit-transition: all 0.25s ease-in-out;
  -moz-transition: all 0.25s ease-in-out;
  -o-transition: all 0.25s ease-in-out;
  -ms-transition: all 0.25s ease-in-out;
  transition: all 0.25s ease-in-out; }

.hover-animation .caption {
  cursor: pointer;
  opacity: 0;
  -webkit-transition: all 0.45s ease-in-out;
  -moz-transition: all 0.45s ease-in-out;
  -o-transition: all 0.45s ease-in-out;
  -ms-transition: all 0.45s ease-in-out;
  transition: all 0.45s ease-in-out; }

.hover-animation .blur {
  position: absolute;
  background-color: rgba(255, 255, 255, 0.9);
  height: 100%;
  width: 100%;
  z-index: 5; }

.hover-animation .caption-text {
  z-index: 10;
  color: #000;
  text-align: center;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%); }
  .hover-animation .caption-text h3 {
    margin-bottom: 0.5em;
    text-transform: uppercase;
    font-size: 1.5em;
    font-weight: 800; }
  .hover-animation .caption-text p {
    font-size: 1.2em; }

/*END of hover animation for tiles*/
/* THE THIRD SECTION */
.story p {
  margin-top: 0.5em;
  font-size: 1.6em;
  line-height: 1.5em; }

/* THE FOURTH SECTION */
.contacts {
  width: 80%;
  margin: 0 auto; }

.contacts__info p {
  margin-top: 3vh;
  line-height: 1.5em; }

.contacts__form {
  float: right;
  margin-bottom: 5em; }

.contacts__form label {
  display: block;
  margin-top: 3vh;
  font-size: 1.3em; }

.contacts__form input[type=text],
input[type=email],
input[type=tel] {
  width: 100%;
  height: 6vh;
  padding: 0.9em 1.2em;
  border: 1px solid #E1E1E1; }

.contacts__form textarea:focus,
input[type=email]:focus,
input[type=tel]:focus,
input[type=text]:focus {
  border: none;
  outline: 1px solid #666; }

.contacts__form textarea {
  width: 100%;
  height: 15vh;
  padding: 0.9em 1.2em;
  border: 1px solid #E1E1E1;
  resize: none; }

.contacts__form-button {
  display: block;
  height: 40px;
  text-transform: uppercase;
  /* font-size: 0.8em; */
  font-weight: bold;
  padding: 0 2em;
  color: #FFFFFF;
  letter-spacing: 0.1em;
  margin-top: 25px;
  border: none;
  background: #1E2023;
  cursor: pointer;
  -webkit-transition: all 0.5s ease-in-out;
  transition: all 0.5s ease-in-out; }

.contacts__form-button:hover {
  opacity: 0.9; }

input:focus:invalid {
  outline: 1px solid red;
  -moz-box-shadow: none; }

input:required:valid,
textarea:required:valid {
  outline: 1px solid green; }

/*FOOTER*/
.footer {
  /* position: absolute; */
  bottom: 0;
  width: 100%;
  text-align: center;
  height: 22vh;
  background: #f1f1f1;
  margin-top: 2em;
  margin-bottom: 0;
  display: flex;
  justify-content: center;
  align-items: center; }

.footer__inner {
  font-size: 1em;
  line-height: 2.5em;
  color: #999999; }

.footer p {
  margin: 0; }

.footer__inner span {
  margin-top: 0;
  color: #000; }

@media only screen and (max-width: 1200px) {
  .home {
    background: url("../img/main_bg_1368.jpg") no-repeat; } }

@media only screen and (max-width: 960px) {
  .home {
    background: url("../img/main_bg_960.jpg") no-repeat; } }

@media only screen and (max-width: 767px) {
  .home {
    background: url("../img/main_bg_768.jpg") no-repeat; }
    .home .inner {
      width: 95%; }
    .home .logo {
      float: left;
      font-size: 2.5em; }
    .home .nav {
      display: none; }
    .home .nav li {
      margin: 0 auto;
      width: 90vw;
      display: block;
      text-align: center; }
    .home .nav li a {
      display: block;
      line-height: 25px;
      font-size: 1.6em;
      text-align: center;
      font-weight: bold;
